.btn-donate {
  position: relative;
  border-radius: 4px;
  border: 1px solid #492FB2;
  padding: 12px 24px;
  background: transparent;
  color: #FFF;
  font-family: Urbanist, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.15px;
  overflow: hidden;
  z-index: 1;
  transition: color 0.3s ease-in-out;
}

.btn-donate::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    130deg,
    #492FB2 -1.74%,
    rgba(73, 47, 178, 0.61) 125.81%
  );
  z-index: -1;
  transition: opacity 0.3s ease-in-out;
  opacity: 1; /* visible by default */
}

.btn-donate:hover {
  color: #492FB2;
    border: 1px solid #492FB2;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color:#492FB2;
}

.btn-donate:hover::before {
  opacity: 0; /* fades out gradient background */
}

/* SVG inside button */
.btn-donate svg path {
  transition: fill 0.3s ease-in-out;
}

.btn-donate:hover svg path {
  fill: #492FB2;
}


.like-button {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
  font-size: 1.5rem;
  color: #ffffff;

  svg {
    width: 20px;
    height: 20px;
    margin-right: 5px;
    fill: none;
    display: inline-block;        // IMPORTANT for transform
    transform-origin: center;
    transform-box: fill-box;      // ensure transform-origin works for svg content
    transition: transform 0.3s;
  }

  &.liked svg path{
    fill: #e0245e;
    stroke: #e0245e;
  }

  &.animate svg {
    animation: pop 0.3s forwards;
  }

  .like-count {
    font-size: 1.2rem;
    transition: color 0.3s;
  }

  &.liked .like-count {
    color: #e0245e;
  }
  &.animate svg {
    animation: pop 0.3s cubic-bezier(.2,.8,.2,1) forwards;
  }
}

@keyframes pop {
  0%   { transform: scale(1); }
  40%  { transform: scale(1.45); }
  65%  { transform: scale(0.95); }
  100% { transform: scale(1); }
}

.breadcrumb-item a{
